    private createGrowthGraph() {
        const dateLabels = this.getDateLabels();
        const gamesPlayed = this.getGamesPlayed(stats["DateRecords"]);
        dateLabels.pop();
        for (let i = 0; i < gamesPlayed.length; i++) {
            gamesPlayed[i].pop();
        }

        for (let i = 0; i < gamesPlayed.length; i++) {
            for (let j = 0; j < gamesPlayed[i].length - 1; j++) {
                const before = gamesPlayed[i][j];
                const after = gamesPlayed[i][j + 1];
                if (before === after) {
                    gamesPlayed[i][j] = 0;
                } else if (before > after) {
                    gamesPlayed[i][j] = ((after / before) - 1) * 100;
                } else {
                    gamesPlayed[i][j] = ((after / before)) * 100;
                }
                // console.log(`${before} / ${after} ${before / after} (${gamesPlayed[i][j]})`);
            }
        }
        dateLabels.pop();
        for (let i = 0; i < gamesPlayed.length; i++) {
            gamesPlayed[i].pop();
        }

        const data = {
            labels: dateLabels,
            datasets: [
                {
                    label: 'Solo',
                    data: gamesPlayed[0],
                    borderColor: 'rgb(255, 99, 132)',
                    backgroundColor: 'rgba(255, 99, 132, 0.5)',
                },
                {
                    label: '2p',
                    data: gamesPlayed[1],
                    borderColor: 'rgb(53, 162, 235)',
                    backgroundColor: 'rgba(53, 162, 235, 0.5)',
                },
                {
                    label: '3p',
                    data: gamesPlayed[2],
                    borderColor: 'rgb(53, 235, 162)',
                    backgroundColor: 'rgba(53, 162, 235, 0.5)',
                },
                {
                    label: '4p',
                    data: gamesPlayed[3],
                    borderColor: 'rgb(235, 162, 53)',
                    backgroundColor: 'rgba(53, 162, 235, 0.5)',
                },
                {
                    label: 'Total',
                    data: gamesPlayed[4],
                    borderColor: 'rgb(0, 0, 0)',
                    backgroundColor: 'rgba(0, 0, 0, 0.5)',
                },
            ],
        };


        const options = {
            responsive: true,
            plugins: {
                legend: {
                    position: 'top' as const,
                },
                title: {
                    display: true,
                },
                scales: {
                    xAxes: [{
                      stacked: false
                    }],
                    yAxes: [{
                      stacked: false,
                      ticks: {
                        callback: function(value: any, index: any, values: any) {
                          return value < 0 ? -value : value;
                        }
                      }
                    }]
                }
            },
        };

        return <Line options={options} data={data}/>;
    }

    private getDateLabels() {
        const startDate = Date.parse("2023-10-09");
        let counter = 0;
        const dateLabels = [];
        while (true) {
            const date = new Date(startDate + (counter * 24 * 60 * 60 * 1000));
            if (date.getTime() >= Date.now()) {
                break;
            }
            dateLabels.push(date.toISOString().substring(0, 10));
            counter++;
        }
        return dateLabels;
    }

